.fadein {
  -webkit-animation: fadein 2s;
  /* Safari and Chrome */

  -moz-animation: fadein 2s;
  /* Firefox */

  -ms-animation: fadein 2s;
  /* Internet Explorer */

  -o-animation: fadein 2s;
  /* Opera */

  animation: fadein 2s;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Firefox */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Safari and Chrome */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
